import isEqual from 'lodash/isEqual';
import { joinName } from './joinName';
// eslint-disable-next-line complexity
export function changedKeys(root, valueA, valueB) {
    if (!valueA || valueA !== Object(valueA) || valueA instanceof Date) {
        return isEqual(valueA, valueB) ? [] : [root];
    }
    var changed = [root];
    if (valueB) {
        for (var key in valueA) {
            if (!(key in valueB) || !isEqual(valueA[key], valueB[key])) {
                changed.push(joinName(root, key));
            }
        }
        for (var key in valueB) {
            if (!(key in valueA)) {
                changed.push(joinName(root, key));
            }
        }
        if (changed.length === 1) {
            changed.pop();
        }
    }
    else {
        // eslint-disable-next-line guard-for-in
        for (var key in valueA) {
            changed.push(joinName(root, key));
        }
    }
    return changed;
}
